import React, { useState } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid'
// import Paper from '@material-ui/core/Paper'
// import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import { connect } from 'react-redux'
import ExportTaleModal from './ExportTaleModal'
import { URL_CREATE_TALE, URL_CREATE_STORYURL } from '../../../utils/urls'
import { InfoCard } from '../../common/cards'
import featureText from './exportFeatureText'
import { Li } from '../../common/misc'
import axios from 'axios'
import {
    actionSetTaleAsWIP
} from '../../../redux/actionCreators/createTaleActions'

const useStyles = makeStyles(theme => ({
    cardHeaders: {
        fontWeight: 500,
        margin: 0,
        textAlign: 'center',
        color: theme.palette.primary.main
    },
    cardContainer: {
        marginTop: theme.spacing(3)
    },
    cardInfo: {
        minHeight: 50,
        marginTop: 3
    }
}))

function download (filename, text) {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
}

const CreateTaleFinish = props => {
    const classes = useStyles()
    const { wipTale, history, setTaleAsWIP, isLocalEdit } = props
    const [isExpTaleModalVisible, setExpTModalVis] = useState(false)
    const [expTale, setExpTale] = useState('')
    const getWIPTale = () => {
        const { storyboard, ...tale } = wipTale
        return tale
    }

    const exportWIPTale = () => {
        if(!areFieldsValid()) return

        const tale = getWIPTale()
        console.log(tale)
        const content = btoa(encodeURIComponent((JSON.stringify(tale))))
        download(`${tale.info.name.replace(/\s+/g, '-')}-${tale.id}.rtl`, content)
        setExpTale(content)
        setExpTModalVis(true)
    }

    const saveTaleLocal = (rtl) => {
        if(!areFieldsValid()) return
        const tale = rtl || getWIPTale()
        const allReactales = JSON.parse(window.localStorage.getItem("allReactales")) || {}
        allReactales[tale.id] = tale
        window.localStorage.setItem("allReactales", JSON.stringify(allReactales))
        alert("Saved succesfully. " + localStorageSpace())
    }

    const saveTaleOnline = async () => {
        if(!areFieldsValid()) return

        const tale = getWIPTale()

        // This variable controls whether a redirection is required to /edit-tale/
        // because of creating new unique storyUrl
        // may be because local / offline tales are being saved for the 1st time
        let needsRedirection = false
        // console.log(tale)

        // If StoryUrl has never been set
        if (!tale.info.storyUrl) {
            let res = await axios.post(URL_CREATE_STORYURL, {storyUrl: tale.info.desiredUrl || ''})
            
            tale.info.storyUrl = res.data.suggestedUrl
            needsRedirection = true
        }

        axios.post(URL_CREATE_TALE, tale)
        .then(res => {
            console.log(res.data)
            let {status, tale} = res.data
            if(status === 200) {
                alert('Tale saved online successfully...')
                tale = JSON.parse(tale)
                setTaleAsWIP(tale)
                if(isLocalEdit) {
                    saveTaleLocal(tale)
                }
                if (needsRedirection) {
                    history.push(`/edit-tale/${tale.info.storyUrl}`)
                }
            }
            else {
                console.log("most probably some error occurred")
            }
        })
        .catch(err =>{
            console.log(err)
        })
    }

    /**
     * It will check throught all the field validation
     * And show message + return boolean depending on it
     */
    const areFieldsValid = rtl => {
        const tale = rtl || getWIPTale()
        const { name, description, genre, lang, tags } = tale.info

        if(!name || !description || !genre || !lang || !tags) {
            alert('Fill all the mandatory fields viz. name, description, genre, language, tags')
            return false
        }
        return true
    }

    const localStorageSpace = () => {
        var allStrings = '';
        for (var key in window.localStorage) {
            if (window.localStorage.hasOwnProperty(key)) {
                allStrings += window.localStorage[key];
            }
        }
        return allStrings ? 3 + ((allStrings.length * 16) / (8 * 1024)) + ' KB' : 'Empty (0 KB)';
    };

    return (
        <Box pb={10}>
            <ExportTaleModal 
                isOpen = {isExpTaleModalVisible}
                handleClose = {() => setExpTModalVis(false)}
                expTaleContent = {expTale}
            />
            <Container maxWidth="md">
                <Grid container spacing={2}>
                    <Grid item xs={12} sm={4} className={classes.cardContainer}>
                        <InfoCard>
                            <h2 className={classes.cardHeaders}>Export Reactale</h2>
                            <span className={classes.cardInfo}>{featureText.exportTale.desc}</span>

                            <h3 className={classes.cardHeaders}>Pros &amp; Cons</h3>
                            <ul className="fillmid">
                                {
                                    featureText.exportTale.features.map( t => 
                                        <Li key={t}>{t}</Li>
                                    )
                                }
                            </ul>

                            <Button
                                variant="contained"
                                color="primary"
                                onClick={exportWIPTale}
                            >
                                Export Reactale
                            </Button>
                        </InfoCard>
                    </Grid>
                    <Grid item xs={12} sm={4} className={classes.cardContainer}>
                        <InfoCard>
                            <h2 className={classes.cardHeaders}>Save Offline</h2>
                            <span className={classes.cardInfo}>{featureText.saveLocal.desc}</span>

                            <h3 className={classes.cardHeaders}>Pros &amp; Cons</h3>
                            <ul className="fillmid">
                                {
                                    featureText.saveLocal.features.map( t => 
                                        <Li key={t}>{t}</Li>
                                    )
                                }
                            </ul>

                            <Button
                                variant="contained"
                                color="primary"
                                onClick={saveTaleLocal}
                            >
                                Save Offline
                            </Button>
                        </InfoCard>
                    </Grid>
                    <Grid item xs={12} sm={4} className={classes.cardContainer} >
                        <InfoCard>
                            <h2 className={classes.cardHeaders}>Save Online</h2>
                            <span className={classes.cardInfo}>{featureText.saveOnline.desc}</span>

                            <h3 className={classes.cardHeaders}>Pros &amp; Cons</h3>
                            <ul className="fillmid">
                                {
                                    featureText.saveOnline.features.map( t => 
                                        <Li key={t}>{t}</Li>
                                    )
                                }
                            </ul>

                            <Button
                                variant="contained"
                                color="primary"
                                onClick={saveTaleOnline}
                            >
                                Save Online
                            </Button>
                        </InfoCard>
                    </Grid>
                </Grid>
            </Container>
            {/* <Button
                variant="contained"
                color="primary"
                className="margin-right-15 margin-top-15"
                onClick={exportWIPTale}
            >
                Export this Reactale
            </Button>
            <Button
                variant="contained"
                color="primary"
                className="margin-right-15 margin-top-15"
                onClick={saveTaleLocal}
            >
                Save in local
            </Button>
            <Button
                variant="contained"
                color="primary"
                className="margin-top-15"
                onClick={saveTaleOnline}
            >
                Save Online
            </Button> */}
        </Box>
    )
}

const mapStateToProps = state => ({
    wipTale: state.wipTale
})
const mapDispatchToProps = dispatch => ({
    setTaleAsWIP: (tale) => dispatch(actionSetTaleAsWIP(tale))
})
export default connect(mapStateToProps, mapDispatchToProps)(CreateTaleFinish)